import {BaseStore} from '@/supreme-core';
let state = {
  payData:{

  },
  payMode: {
    point: '',
    cash: '',
    mix: {
      point: '',
      cash: ''
    }
  },
  goodsPageData: {
    skuList: {
      codes: [
        {
          spuCode: '1000521',
          skuCode: '1000521',
          amount: '1',
        }
      ]
    },
    goodsInfo: {
      imgUrl: '',
      goodsName: 'NIO原厂店址车窗隔热膜',
      goodsType: '臻选 前挡+侧前+天窗 侧前高透',
      goodsPoint: '4000',
      goodsPrice: '400',
    },
    relatedCarInfo: {
      carName: 'ES8创始版·深空灰',
      carOrderNo: '810001536545795075'
    }
  }
};
let mutations = {
  setPayData(state,data){
    state.payData = data
  },
};

var store= BaseStore.getInstance({
  state: state,
  mutations:mutations
});

export default {
  namespaced: true,
  state: state,
  mutations: mutations
}



