import {BaseModel,supreme} from '@/supreme-core'
import util from '@/util/util';
var envInfo=util.getEnvInfo("envConfig")
// console.log(envInfo);
class CommonModel extends BaseModel{
  constructor(config){
    //config.baseURL=config.baseURL|| envInfo.baseUrl + config.api
    config.baseURL = config.baseURL || envInfo.baseUrl + envInfo.application+"/"+ encodeURIComponent(config.api)
    super(config);
  }
  post(param){
    if(!param){
      param={};
    }
    // var signObj = generateSign.default()
    // param["token"]=supreme.sessionCache.get("token").token;
    // param["timestamp"]=signObj.timestamp;
    // param["sign"]=signObj.getwaySign;
    // param["_sid"]=supreme.sessionCache.get("user")._sid;
    // supreme.sessionCache.get("accessToken");
    // param["loginAccount"]= '224047720';
    let config ={}
    if(supreme.sessionCache.get("accessToken")){
      // supreme.sessionCache.get("accessToken")
      // "2.02u/oD8bQsbipcX22Nw6wY39Wtg6cLcFNyRkO5GOaPyQ="
      config={
        headers:{
          "token": supreme.sessionCache.get("accessToken")
        }
      }
    }
    if(param.Data){
      param.Data=JSON.stringify(param.Data)
    }
    return super.post(param, config);
  }
  get(param){

  }
}
export default CommonModel;



