import CryptoJS from 'crypto-js';
import util from '@/util/util'
var envInfo=util.getEnvInfo("nrsEnvConfig")

const getwayKey=envInfo.gatwayKey;
// const bizKey=env.bizKey;
const generateSign = function(params){
  var time = Date.parse(new Date());
  var timestamp = Date.parse(new Date());
  var getwayStr = 'application='+envInfo.application+'&timestamp='+timestamp+getwayKey;
  //var bizStr = '_sid=' + BaseView.sessionCache.get('user')._sid + '&';
  /*var bizStr = 'timestamp=' + time;
  bizStr = bizStr.concat(bizKey);*/
  return {
    //bizSign: CryptoJS.MD5(bizStr).toString().toUpperCase(),
    getwaySign:CryptoJS.MD5(getwayStr).toString().toUpperCase(),
    timestamp: time
  }
}
export default generateSign



