<template>
  <transition name="slide-fade">
    <div class="confirmOrderAlert">
      <div class="title">
        <div>提示</div>
        <div>
          <img src="../../assets/images/close_icon.png" @click="close">
        </div>
      </div>
      <div class="content">
        {{ content }}
      </div>
      <div class="buttons">
        <div>
          <div @click="close">取消</div>
          <div>确认提交</div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: "ConfirmOrderAlert",
    data(){
        return {
          content: '订单提交后积分将自动扣除，是否确认提交？'
        }
    },
    methods: {
      close(){
        this.$emit('cancel-confirmOrderAlert')
      }
    }
  }
</script>

<style scoped lang="scss">
  .confirmOrderAlert{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    background-color: #fff;
    z-index: 102;
    >div{
      width: 100%;
      padding: 0 25px;
    }
    .title{
      height: 60px;
      color: #363C54;
      font-size: 16px;
      border-bottom: 1px solid #F0F1F2;
      padding-right: 23px;
      >div:nth-child(1){
        float: left;
        height: 100%;
        line-height: 60px;
        color: #363C54;
        font-size: 16px;
      }
      >div:nth-child(2){
        float: right;
        height: 100%;
        color: #363C54;
        font-size: 16px;
        display: flex;
        align-items: center;
        img{
          height: 19px;
        }
      }
    }
    .content{
      height: 115px;
      border-bottom: 1px solid #F0F1F2;
      padding-top: 25px;
      padding-bottom: 25px;
      color: #696D7F;
      font-size: 14px;
    }
    .buttons{
      height: 70px;
      padding-top: 13px;
      padding-bottom: 13px;
      >div{
        width: 100%;
        height: 100%;
        >div{
          width: 48.6%;
          height: 100%;
          border-radius: 23px;
          font-size: 16px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        >div:nth-child(1){
          float: left;
          border:1px solid rgba(205,206,212,1);
          color: #696D7F;
        }
        >div:nth-child(2){
          float: right;
          border:1px solid #00BEBE;
          background-color: #00BEBE;
          color: #fff;
        }
      }
    }
  }

  /*******滑动效果********/
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s ease;
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(250px);
    opacity: 0;
  }
</style>



