import {BaseStore} from '@/supreme-core';
let state = {
  invoiceData:{
    type: null,
    data: {}
  }
};
let mutations = {
  setInvoiceData(state,data){
    state.invoiceData = data
  },
};

var store= BaseStore.getInstance({
  state: state,
  mutations:mutations
});

export default {
  namespaced: true,
  state: state,
  mutations: mutations
}



