<template>
  <div class="proInfo">
    <div>
      <div>
        <img :src="goodsInfo.imgUrl">
      </div>
      <div>
        <span>{{ goodsInfo.goodsName }}</span>
        <span>{{ goodsInfo.goodsType }}</span>
        <div class="goods-price" style="float: left;">
          <span class="integral3"></span>
          <span :style="colorStyle3">{{ goodsInfo.goodsPoint }}</span>
        </div>
        <div class="plus" style="margin-right: 0;width: unset;align-items: unset;">(</div>
        <div class="goods-price" style="float: left" :style="colorStyle3">
          <span class="textStyle">￥</span>
          <span>400</span>
        </div>
        <div class="plus" style="margin-left: 0;width: unset;align-items: unset;">)</div>
      </div>
      <div><span>X</span><span>{{ goodsInfo.goodsNumber }}</span></div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import {baseMixin, hybridFn, supreme} from '@/supreme-core'
  export default {
    data(){
      return {
        //字体颜色
        colorStyle1: {
          color: "#9B9DA9"
        },
        colorStyle2: {
          color: "#00BCBC"
        },
        colorStyle3: {
          color: "#363C54"
        },
        goodsInfo: {}
      }
    },
    /*computed: {
      ...mapState({
        //商品信息
        'goodsInfo': state => state.confirmOrderState.goodsPageData.goodsInfo,
      })
    },*/
    created(){
      let data = supreme.sessionCache.get("orderData");
      this.goodsInfo = data.goodsInfo
    },
  }
</script>

<style scoped lang="scss">
  .proInfo{
    width: 100%;
    padding: 20px 25px;
    border-bottom: 5px solid #F7F7F8;
    >div{
      width: 100%;
      height: 100px;
      padding-top: 20px;
      >div{
        float: left;
      }
      >div:nth-child(1){
        width: 80px;
        height: 80px;
        border-radius:2px;
        img{
          height: 100%;
        }
      }
      >div:nth-child(2){
        width: calc(90% - 80px);
        height: 80px;
        padding-left: 15px;
        >span{
          display: inline-block;
          width: 100%;
          height: 20px;
          color: #363C54;
          font-size: 14px;
        }
        >span:nth-child(1){
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
        >span:nth-child(2){
          height: 40px;
          font-size: 12px;
          color: #9B9DA9;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
      }
      >div:nth-child(3){
        width: 10%;
        height: 80px;
        color: #363C54;
        font-size: 14px;
        text-align: right;
        span:nth-child(1){
          margin-right: 3px;
        }
      }
    }
    >div:nth-child(1){
      height: 80px;
      padding-top: 0;
    }
  }
  .plus{
    float: left;
    width: 8px;
    height: 17px;
    font-size: 14px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 5px;
  }
  .goods-name{
    display: flex;
    align-items: center;
    margin: 12px 32px 0;
    height: 34px;
    text-align: center;
  }
  .goods-price{
    margin-top: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: #666666;
    .integral,.integral2,.integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_blue_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .integral2{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_icon.png') top center no-repeat;
      background-size:14px;
    }
    .integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_grey_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .textStyle{
      width: 14px;
      height: 14px;
      line-height: 14px;
    }
  }
</style>



