<template>
  <div class="confirm-order-page" @back="backToLastPage">
    <header-nav :shareShow="false" v-if="isInWebView"></header-nav>
    <div class="container">
      <div class="title">确认订单</div>
      <!--<div class="logisInfo">
        <input class="logisInfo-input-test" placeholder="测试openurl在这输入" type="text" v-model="openUrlData" />
      </div>-->
      <wireframe-page
        :componentsList="wireframeComponentsList"
        :dataList="wireframeDataList"
      ></wireframe-page>
      <!--价格展示-->
      <div class="priceShow">
        <div>
          <div>商品总额：</div>
          <div>
            <div class="goods-price">
              <span class="integral3"></span>
              <span :style="colorStyle3">0</span>
              <div class="plus" :style="colorStyle3">+</div>
              <div class="goods-price" :style="colorStyle3">
                <span class="textStyle">￥</span>
                <span>{{ parseInt(payData.totalPoint)/parseInt(payData.exchangeRate) }}</span>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div>运费金额：</div>
          <div>
            <div class="goods-price" style="float: left" :style="colorStyle3">
              <span class="textStyle">￥</span>
              <span>{{ (parseInt(payData.payPoint) - parseInt(payData.totalPoint))/parseInt(payData.exchangeRate) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--底部下单-->
    <div class="count">
      <div>
        <div>
          <div style="padding-left: 3px;">总计：</div>
          <div style="margin-top: 2px;">
            <!--<div class="goods-price" style="float: left">
              <span class="integral"></span>
              <span :style="colorStyle2">6,980</span>
            </div>
            <div class="plus" :style="colorStyle2">+</div>-->
            <div class="goods-price" style="float: left" :style="colorStyle2">
              <span class="textStyle">￥</span>
              <span>{{ parseInt(payData.payPoint)/parseInt(payData.exchangeRate) }}</span>
            </div>
          </div>
        </div>
        <div>
          <div @click="submitOrder">提交订单</div>
        </div>
      </div>
    </div>

    <!--选择支付方式-->
    <pay-mode-select v-if="payModeSelectShow" @cancel-payModeSelect="cancelPayModeSelect" @change-payModeSelect="changePayModeSelect"></pay-mode-select>
    <!--下单提示-->
    <confirm-order-alert v-if="confirmOrderAlertShow" @cancel-confirmOrderAlert="cancelConfirmOrderAlert"></confirm-order-alert>
    <!--遮罩层-->
    <transition name="fade">
      <div class="mask" v-if="maskShow" @click="maskHide"></div>
    </transition>
  </div>
</template>

<script>
  //import hybrid from '@/util/reactNativeHybrid'
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import {baseMixin, hybridFn, supreme} from '@/supreme-core'
  import HeaderNav from "../../components/header-nav";
  import PayModeSelect from "./PayModeSelect";
  import ConfirmOrderAlert from "./ConfirmOrderAlert";
  import { submitOrderModel,getConfirmOrderInfoModel,getTotalPriceModel } from '@/model';
  import util from '@/util/util'
  import baseConfig from '@/config/baseConfig'
  var pathName=baseConfig.compatible.prefix+ baseConfig.compatible.module;
  import WireframePage from "./WireframePage";
  export default {
    name: "Index",
    mixins:[baseMixin],
    components: {
      ConfirmOrderAlert,
      PayModeSelect,
      HeaderNav,
      WireframePage
    },
    data(){
      return {
        wireframeComponentsMap: {
          "delivery_address": "v-logis-info",
          "payment_method": "v-pay-mode",
          "goods_message": "v-products-info",
          "associated_vehicle": "v-car-order-info",
          "my_invoice": "v-my-invoice",
        },
        openUrlData:'',
        wireframeComponentsList: [],
        wireframeDataList: [],
        pageData: [],
        //字体颜色
        colorStyle1: {
          color: "#9B9DA9"
        },
        colorStyle2: {
          color: "#00BCBC"
        },
        colorStyle3: {
          color: "#363C54"
        },
        //模块显示
        payModeSelectShow: false,
        confirmOrderAlertShow: false,
        maskShow: false,

        /******页面数据*******/
        //下单地址
        addressId: '',
        shipping_address: {

        },
        //支付金额
        payData: {
          payModeStatus: 1, //1纯积分2混合3纯现金
          exchangeRate: 1,
          minMoney: 0,
          minPoint: 0,
          payPoint: 0,
          totalPoint: 0,
        },
        //发票信息
        invoiceStyle: {
          style: {
            color: '#9B9DA9',
          },
          text: '请填写发票信息'
        },
        invoice: {

        },
        relatedCarInfo: {},
        skuListInfo: []
      }
    },
    computed: {
      isInWebview(){
        return util.isInWebview();
      }
    },
    appear(data){
      alert('1appear:'+JSON.stringify(info))
      console.log('this is vue appear...........');
      console.log(JSON.stringify(data));
    },
    created(){
      dsBridge.register('appear',function(info){
        alert("1appear:"+JSON.stringify(info))
      });
     // console.log(supreme.sessionCache.get("orderData"))
      let data = supreme.sessionCache.get("orderData");
      this.relatedCarInfo = data.relatedCarInfo;
      this.skuListInfo = data.skuList
    },
    mounted(){
      supreme.pubHubus.$off('change-pay-mode');
      supreme.pubHubus.$on('change-pay-mode',(val)=>{
        this.handlerPayModeChange()
      });
      this.getConfirmOrderInfo();
    },
    activated(){

    },
    methods:{
      ...mapMutations({
        'setPayData':"confirmOrderState.setPayData",
      }),
      backToLastPage(){
        util.isInWebview()&&hybrid.close();
      },
      //获取楼层信息
      getConfirmOrderInfo(){
        getConfirmOrderInfoModel.post({
          /*Data: {
            "carOrderNo": "810001536545795075",
            "codes": [{
              "skuCode": "1000521",
              "amount": "1",
              "specialAttributes": [{"attributeCode": "ATT2018101700000003", "valueCode": "ATV2018101700000012"}]
            }]
          }*/
          Data: {
            carOrderNo: this.relatedCarInfo.carOrderNo,
            codes: this.skuListInfo
          }
        }).then(data => {
       //   console.log(data)
          this.pageData = data.data.resultData;
          this.initPageData();
        }).catch(err => {

        })
      },
      //初始化页面数据
      initPageData(){
        let pageData = this.pageData;
        pageData.forEach((item,index) => {
          if(this.wireframeComponentsMap[item.type] && item.data.isShow){
            this.wireframeComponentsList.push(this.wireframeComponentsMap[item.type]);
            this.wireframeDataList.push(item.data)
          }
          if(item.type === 'payment_method'){
            this.payData = item.data;
        //    this.setPayData(this.payData)
          }
        })
      //  console.log(this.wireframeDataList)
      },
      /*//获取价格数据
      getTotalPrice(){
        getTotalPriceModel.post({
          Data: [{
            "skuCode": "1000314",
            "amount": 1,
            "specialAttributes": [
              {
                "attributeCode": "ATT2018102400000002",
                "valueCode": "ATV2018102400000021"}
              ]
          }]
        }).then(data => {
          let info = data.data.resultData;
          this.payData = info;
        }).catch(err => {

        })
      },*/
      handlerPayModeChange(){
        this.maskShow = true;
        this.payModeSelectShow = true;
      },
      cancelPayModeSelect(){
        this.payModeSelectShow = false;
        this.maskShow = false;
      },
      changePayModeSelect(val){
        this.payModeSelectShow = false;
        this.maskShow = false;
      //  console.log(val)
      },
      //跳转发票页面
      selectInvoice(){
        this.goto(pathName+'/myInvoice',{})
      },
      //下单
      cancelConfirmOrderAlert(){
        this.confirmOrderAlertShow = false;
        this.maskShow = false;
      },
      submitOrder(){
        let submitData = {
          shipping_address: this.shipping_address,
          sku_list: this.skuListInfo,
          entrance: 'shoppingDetails',
          cash_amount: parseInt(this.payData.payPoint)/parseInt(this.payData.exchangeRate),
          vehicleOrder: this.relatedCarInfo,
          invoice: this.invoice
        };
        submitOrderModel.post({
          Data: submitData
          /*Data: {
            "shipping_address": {
              "area": {"province": "北京市", "city": "北京市", "county": "东城区"},
              "country_code": "86",
              "phone": "18175596465",
              "name": "给你了",
              "id": 10114,
              "detail": "101",
              "is_default": true
            },
            "sku_list": [{"spu_code": "TG20180824000004", "sku_code": "1000004", "specialAttributes": [], "amount": 5}],
            "entrance": "shoppingDetails",
            "cash_amount": '',
            "vehicleOrder": {"carName": "ES8创始版·深空灰", "carOrderNo": "810001536545795075"},
            "invoice": {"type": 2, "companyName": "1111", "orgCode": "1111", "email": "111"}
          }*/
        }).then(data => {
          console.log(data)
          //调取下单接口回调
          // dsBridge.call("NIODoBridge.openRnPay",'nio://mall/pay?orderId=xxxxx&credit=3&cash=3&source=""&type="carmall"', function (val) {
          //   alert(val.code);
          // })
          dsBridge.call("NIODoBridge.openURL",'nio://mall/pay?orderId=xxxxx&credit=3&cash=3&source=""&type=carmall',()=>{

          });
        }).catch(err => {

        });
        /*if(this.payModeStatus ===3){

        }else {

        }
        this.confirmOrderAlertShow = true;
        this.maskShow = true;*/
      },
      maskHide(){
        this.payModeSelectShow = false;
        this.confirmOrderAlertShow = false;
        this.maskShow = false;
      },
    }
  }
</script>

<style scoped lang="scss">
  .logisInfo-input-test{
    width: 100%;
    height: 60px;
    border: 1px solid #02B0B0;
  }
  .confirm-order-page{
    touch-action: none;
    z-index: 100;
    width: 100%;
    height: 100%;
    color: #363C54;
    background-color: #fff;
    overflow-x: hidden;
    overflow-y: auto;
    .container{
      >div:nth-child(1){
        margin-top: 64px;
      }
     > div:nth-last-child(1){
       margin-bottom: 70px;
     }
    }
    .title{
      margin-left: 25px;
      font-size: 32px;
      margin-bottom: 20px;
    }
    .priceShow{
      width: 100%;
      height: 80px;
      padding: 15px 25px;
      color: #363C54;
      font-size: 14px;
      >div{
        width: 100%;
        height: 25px;
        >div{
          height: 100%;
          line-height: 25px;
        }
        >div:nth-child(1){
          float: left;
        }
        >div:nth-child(2){
          float: right;
        }
      }
    }
    .count{
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 70px;
      padding: 0 25px;
      border-top: 1px solid #F7F7F8;
      background-color: #fff;
      >div{
        width: 100%;
        height: 100%;
        >div:nth-child(1){
          float: left;
          width: 60%;
          padding: 15px 0;
        }
        >div:nth-child(2){
          float: right;
          width: 40%;
          height: 100%;
          margin: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          div{
            width: 100%;
            height: 45px;
            font-size: 15px;
            line-height: 45px;
            text-align: center;
            background-color: #02B0B0;
            color: #fff;
            border-radius: 45px;
          }
        }
      }
    }
  }
  .plus{
    float: left;
    width: 8px;
    height: 17px;
    font-size: 14px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 5px;
  }
  .goods-name{
    display: flex;
    align-items: center;
    margin: 12px 32px 0;
    height: 34px;
    text-align: center;
  }
  .goods-price{
    margin-top: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: #666666;
    .integral,.integral2,.integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_blue_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .integral2{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_icon.png') top center no-repeat;
      background-size:14px;
    }
    .integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_grey_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .textStyle{
      width: 14px;
      height: 14px;
      line-height: 14px;
    }
  }
  .mask{
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    bottom: 0;
    z-index: 101;
    background-color: #000000;
    opacity: .3;
  }

  /******动画效果*******/
  .fade-enter-active {
    animation: fade-in .5s;
  }
  .fade-leave-active {
    animation: fade-in .4s reverse;
  }
  .slide-enter-active {
    animation: slide-in 0.5s;
  }
  .slide-leave-active {
    animation: slide-in 0.4s reverse;
  }
  @keyframes fade-in-out {
    0% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.6;
    }
    100% {
      opacity: 0;
    }
  }
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.3;
    }
  }
  @keyframes slide-in {
    0% {
      transform: translateY(100vh);
    }
    100% {
      transform: translateY(0);
    }
  }
</style>



