<template>
  <div class="goods-page" v-if="goodsState">
    <!-- 导航 -->
    <headerNav @back="backToLastPage" v-if="!isInWebview"/>
    <!-- banner -->
    <Banner />
    <div class="goods-content">
      <!-- title -->
      <p class="goods-title">{{getTitle}}</p>
      <!-- tag -->
      <ul class="goods-tag-ul">
        <li v-for="(tag,tagIndex) in selectedSku.tag" :key="tagIndex">{{tag.title}}</li>
      </ul>
      <!-- 价格 -->
      <div class="goods-price">
        <span>¥{{selectedSku.price&&selectedSku.price.currentUserPriceCash}}</span>
        <span class="integral">{{selectedSku.price&&selectedSku.price.currentUserPricePoint}}</span>
      </div>
      <!-- 规格 button -->
      <div class="goods-specifications" @click="toggleSpecLayer">
        <span>规格</span>
        <span v-if="!selectedAttr" class="select-text">选择规格数量</span>
        <span v-else class="select-text cur">{{selectedAttr}}</span>
        <span class="arrow"></span>
      </div>
      <!-- 文描 -->
      <Describe :content="getDescribeText" />
      <SpecLayer @close="toggleSpecLayer" :specLayerShow="specLayerShow" />
      <div v-show="specLayerShow" class="spec-layer-mask"></div>
      <!-- 底部浮动导航 -->
      <div class="goods-footer">
        <span class="service"></span>
        <span class="phone"></span>
        <div @click="gotoOrder" class="buy-button" :class="{disabled:isHasCar}">
          <span>立即购买</span>
        </div>
        <div v-if="isHasCar" class="hint">该商品仅限已关联车辆的用户购买</div>
      </div>
      <!-- 选车弹层 -->
      <SelectCarLayar  />
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {mapState, mapGetters, mapMutations, mapActions } from 'vuex';
  import {baseMixin,hybridFn,supreme} from '@/supreme-core'
  import util from '@/util/util'
  import Banner from './Banner.vue'
  import headerNav from '@/components/header-nav.vue';
  import Describe from '@/components/describe.vue';
  import SpecLayer from './SpecLayer.vue';
  import SelectCarLayar from './SelectCarLayar.vue';
  import { productDetailModel } from '@/model'
  import baseConfig from '@/config/baseConfig'
  var envInfo=util.getEnvInfo("envConfig")
  var pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
  export default {
    name: "goodsPage",
    mixins:[baseMixin],
    data(){
      return{
        specLayerShow:false,//规格层显示条件
      }
    },
    created:function(){
      let params = {
        Data:{ spuCode:this.$route.query.pageCode}
      }
      this.getGoodsData(params);
    },
    appear(data){
      supreme.showToast('goods page  appear...........');
      console.log(data);
    },
    components:{
      Banner,
      headerNav,
      Describe,
      SpecLayer,
      SelectCarLayar
    },
    computed:{
      ...mapState({
        'goodsState': state => state.goodsState.data,
        'pageState': state => state.goodsState.pageState
      }),
      ...mapGetters({
        selectedAttr: 'goodsState/selectedAttr',
        currentSelectCar: 'goodsState/currentSelectCar'
      }),
      isInWebview(){
        return util.isInWebview();
      },
      getTitle(){
        let text = "";
         this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "title_price"){
            text = item.data.mainTitle;
          }
        })
        return text;
      },
      getBannerUrl(){
        let url = "";
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "carousel"){
            url = item.data[0].thumbnailURL;
          }
        })
        return url;
      },
      selectedSku(){
        let obj = {};
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"){
            obj = item.data.selectedSku
          }
        })
        return obj;
      },
      isHasCar(){
        let isShow = true;
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList.length>1){
            isShow = false;
          }
        })
        return isShow;
      },
      getDescribeText(){
        let textHtml = "";
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item,index)=>{
          if(item.type === "goods_info"){
            textHtml = item.data.content;
          }
        })
        return decodeURIComponent(textHtml)
      },
    },
    methods:{
      ...mapMutations({
        setGoodsData: "goodsState/setGoodsData"
      }),
      ...mapActions({
        getGoodsData: 'goodsState/getGoodsData' // 将 `this.add()` 映射为 `this.$store.dispatch('increment')`
      }),
      backToLastPage(){
        //this.$router.back();
        if(util.isInWebview()){

        }else{
          this.back();
        }
      },
      toggleSpecLayer(){
        this.specLayerShow = !this.specLayerShow;
      },
      gotoOrder(){
        if(this.isHasCar){
          supreme.showToast("只限车主购买");
          return;
        }
        let confirmOrderUrl = `${envInfo.openUrl}${pathName}/confirmOrder?wv=do`;
        let orderData = {
              goodsInfo: {
                imgUrl: this.getBannerUrl,
                goodsName: this.getTitle,
                goodsType: this.selectedAttr,
                goodsPoint: this.selectedSku.price.currentUserPricePoint,
                goodsNumber: this.pageState.amount
              },
              relatedCarInfo: {
                carName: `${this.currentSelectCar.vehicleInfo.carName}·${this.currentSelectCar.vehicleInfo.carColor}`,
                carOrderNo: this.currentSelectCar.carOrderNo
              },
              skuList: [
                {
                  spuCode: this.selectedSku.spuCode,
                  skuCode: this.selectedSku.skuCode,
                  amount: this.pageState.amount
                }
              ]
            };
        // 设置订单数据
        supreme.sessionCache.set("orderData",orderData);
        if(util.isInWebview()){
          dsBridge.call("NIODoBridge.openURL", confirmOrderUrl);
        }else{
          this.goto(pathName+"/confirmOrder");
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .goods-page{
    height: 100%;
    width: 100%;
    overflow-x: auto;
    background-color: #fff;
  }
  .goods-content{
    margin: 0 25px;
    .goods-title{
      margin-top: 25px;
      font-size: 24px;
      color: #363C54;
    }
    .goods-tag-ul{
      margin-top: 10px;
      display: flex;
      li{
        height: 30px;
        line-height: 30px;
        padding: 0 15px;
        margin-right: 10px;
        color: #9B9DA9;
        background-color: #F7F7F8;
        border-radius: 15px;
      }
    }
    .goods-price{
      margin-top: 10px;
      // display: flex;
      font-size: 20px;
      color: #363C54;
      span{
        margin-right: 10px;
      }
      .integral{
        padding-left: 22px;
        width: 20px;
        height: 20px;
        background: url("../../assets/images/integral2_icon.png") left 5px no-repeat;
        background-size: 18px 16px;
      }
    }
    .goods-specifications{
      position: relative;
      margin-top: 25px;
      height: 55px;
      display: flex;
      align-items: center;
      color: #9B9DA9;
      font-size: 16px;
      border-top: 1px solid #F0F1F2;
      border-bottom: 1px solid #F0F1F2;
      .arrow{
        position: absolute;
        right: 5px;
        top: 23px;
        width: 6px;
        height: 10px;
        background: url("../../assets/images/right_arrow_icon.png") center no-repeat;
        background-size: 6px 10px;
      }
      .select-text{
        margin-left: 15px;
        color: #CDCED4;
        &.cur{
          color: #363C54;
        }
      }
    }
  }
  .spec-layer-mask{
    position: absolute;
    left: 0;
    bottom: 0;
    height:100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .2);
    z-index: 99;
  }
  .goods-footer{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 25px 0 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    background-color: #fff;
    z-index: 101;
    .phone{
      width: 48px;
      height: 48px;
      background: url("../../assets/images/phone_icon.png") center no-repeat;
      background-size: 20px;
    }
    .service{
      width: 48px;
      height: 48px;
      background: url("../../assets/images/service_icon.png") center no-repeat;
      background-size: 20px;
    }
    .buy-button{
      margin-left: 20px;
      flex: 1;
      height: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      color: #fff;
      border-radius: 23px;
      background-color: #00BEBE;
      &.disabled{
        background-color: #CDCED4;
      }
    }
    .hint{
      position: absolute;
      top: -40px;
      left: 0;
      width: 100%;
      padding-left: 25px;
      height: 40px;
      line-height: 40px;
      background-color: #F0FCFC;
      font-size: 14px;
      color: #00BCBC;
    }
  }
</style>



