/**
 * @description
 * */
import api from './api';
import CommonModel from './CommonModel'
import NrsCommonModel from './NrsCommonModel'
import TokenModel from './TokenModel'

const tokenModel = new TokenModel();
const findTodayAddAndPostponeTaskModel=new CommonModel({url:api.findTodayAddAndPostponeTask});
const testModel=new NrsCommonModel({url:api.findTodayAddAndPostponeTask});
const portfolioListModel=new NrsCommonModel({api:api.portfolioList});
const homeListModel=new CommonModel({api:api.homeList});
const productDetailModel=new CommonModel({api:api.productDetail});

//下单相关
const getDetailAddressModel=new CommonModel({api: api.getDetailAddress});
const getTotalPriceModel=new CommonModel({api: api.gettotalprice});
const submitOrderModel=new CommonModel({api: api.placeanorderbycart});
const getConfirmOrderInfoModel=new CommonModel({api: api.getConfirmOrderInfo});


export {
    tokenModel,
    findTodayAddAndPostponeTaskModel,
    testModel,
    portfolioListModel,
    homeListModel,
    productDetailModel,
    //下单相关
    getDetailAddressModel,
    getTotalPriceModel,
    submitOrderModel,
    getConfirmOrderInfoModel,
};



