// 微服务路径
// let apiurl = 'api/v1/otd/mer/service-aggregation';
function apiurl(url){
  return 'api/v1/otd/mer/service-aggregation'+url;
}
export default {
  login: 'doLogin',
  ssoLogin: 'cl/app/sso/login',
  /*任务模块*/
  findTodayAddAndPostponeTask:"task/findTodayAddAndPostponeTask",
  portfolioList:"nrs/portfolio/getList",
  homeList:apiurl("/homepage/noauth/index"),
  productDetail:apiurl("/product/noauth/cmsdetail"),

  //下单相关
  placeanorderbycart: '/order/placeanorderbycart',
  gettotalprice: apiurl('/order/gettotalprice'),
  getDetailAddress: apiurl('/userinfo/addressdetail'),
  getConfirmOrderInfo: apiurl('/order/page'),
}



