<template>
  <transition name="slide-fade">
    <div class="payModeSelect">
      <div class="title">
        <div>付款方式</div>
        <div>
          <img :src="img" @click="close">
        </div>
      </div>
      <!--付款方式-->
      <div class="payMode">
        <div>
          <div :class="pointStyle" @click="changeMode(1)">
            <div class="goods-price" style="float: left">
              <span class="integral"></span>
              <span>6,980</span>
            </div>
          </div>
          <div :class="mixStyle" @click="changeMode(2)">
            <div class="goods-price" style="float: left">
              <span class="integral"></span>
              <span>6,980</span>
            </div>
            <div class="plus">+</div>
            <div class="goods-price" style="float: left">
              <span class="textStyle">￥</span>
              <span>3600</span>
            </div>
          </div>
        </div>
        <div>
          <div :class="cashStyle" @click="changeMode(3)">
            <div class="goods-price" style="float: left">
              <span class="textStyle">￥</span>
              <span>3600</span>
            </div>
          </div>
        </div>
      </div>
      <div class="confirmButton">
        <div @click="confirm">确定</div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Img from '@/assets/images/close_icon.png'
  export default {
    name: "payModeSelect",
    data(){
      return {
        img: Img,
        pointStyle: {
          disable: false,
          enable: true,
          choosed: false
        },
        mixStyle: {
          disable: false,
          enable: false,
          choosed: true
        },
        cashStyle: {
          disable: false,
          enable: true,
          choosed: false
        },
      }
    },
    methods:{
      close(){
        this.$emit('cancel-payModeSelect')
      },
      confirm(){
        this.$emit('change-payModeSelect',1)
      },
      //切换付款方式（可以用watch简化）
      changeMode(val){
        if(val === 1){
          if(this.pointStyle.disable){
            return
          }else {
            this.pointStyle.enable = false;
            this.pointStyle.choosed = true;
            if(!this.mixStyle.disable){
              this.mixStyle.enable = true;
              this.mixStyle.choosed = false;
            }
            if(!this.cashStyle.disable){
              this.cashStyle.enable = true;
              this.cashStyle.choosed = false;
            }
          }
        }else if(val === 2){
          if(this.mixStyle.disable){
            return
          }else {
            this.mixStyle.enable = false;
            this.mixStyle.choosed = true;
            if(!this.pointStyle.disable){
              this.pointStyle.enable = true;
              this.pointStyle.choosed = false;
            }
            if(!this.cashStyle.disable){
              this.cashStyle.enable = true;
              this.cashStyle.choosed = false;
            }
          }
        }else if(val === 3){
          if(this.cashStyle.disable){
            return
          }else {
            this.cashStyle.enable = false;
            this.cashStyle.choosed = true;
            if(!this.pointStyle.disable){
              this.pointStyle.enable = true;
              this.pointStyle.choosed = false;
            }
            if(!this.mixStyle.disable){
              this.mixStyle.enable = true;
              this.mixStyle.choosed = false;
            }
          }
        }
      }
    },
    /*watch:{
     pointStyle: {
       handler(n,o){
         let a,flag;
         for(let i in n){
           if(n[i] !== o[i]){
             a = i;
             flag = n[i];
             break
           }
         }
         debugger
         for(let i in this.pointStyle){
           if(i === a){
             this.pointStyle[i] = flag
           }else{
             this.pointStyle[i] = !flag
           }
         }
         console.log(this.pointStyle)
       },
       deep: true
     },
     mixStyle: {
       handler(n,o){
         let a,flag;
         for(let i in n){
           if(n[i] !== o[i]){
             a = i;
             flag = n[i];
             break
           }
         }
         for(let i in this.pointStyle){
           if(i === a){
             this.pointStyle[i] = flag
           }else{
             this.pointStyle[i] = !flag
           }
         }
       },
       deep: true
     },
     cashStyle: {
       handler(n,o){
         let a,flag;
         for(let i in n){
           if(n[i] !== o[i]){
             a = i;
             flag = n[i];
             break
           }
         }
         for(let i in this.pointStyle){
           if(i === a){
             this.pointStyle[i] = flag
           }else{
             this.pointStyle[i] = !flag
           }
         }
       },
       deep: true
     }
   },*/
  }
</script>

<style scoped lang="scss">
  .payModeSelect{
    touch-action: none;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    background-color: #fff;
    z-index: 102;
    >div{
      width: 100%;
      padding: 0 25px;
    }
    .title{
      height: 60px;
      color: #363C54;
      font-size: 16px;
      border-bottom: 1px solid #F0F1F2;
      padding-right: 23px;
      >div:nth-child(1){
        float: left;
        height: 100%;
        line-height: 60px;
        color: #363C54;
        font-size: 16px;
      }
      >div:nth-child(2){
        float: right;
        height: 100%;
        color: #363C54;
        font-size: 16px;
        display: flex;
        align-items: center;
        img{
          height: 19px;
        }
      }
    }
    .payMode{
      height: 120px;
      border-bottom: 1px solid #F7F7F8;
      >div{
        width: 100%;
        height: 60px;
        >div{
          float: left;
          height: 100%;
          border-radius:15px;
          border:1px solid rgba(205,206,212,1);
          display: flex;
          align-items: center;
          padding: 0 20px;
        }
      }
      >div:nth-child(1){
        padding-top: 20px;
        padding-bottom: 10px;
        >div:nth-child(1){
          margin-right: 10px;
        }
      }
      >div:nth-child(2){
        padding-top: 10px;
        padding-bottom: 20px;
      }
    }
    .confirmButton{
      height: 70px;
      border-bottom: 1px solid #F7F7F8;
      padding: 13px 25px;
      >div{
        width: 100%;
        height: 100%;
        border-radius: 23px;
        background-color: #00BEBE;
        color: #fff;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
  .plus{
    float: left;
    width: 8px;
    height: 17px;
    font-size: 14px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 5px;
  }
  .goods-name{
    display: flex;
    align-items: center;
    margin: 12px 32px 0;
    height: 34px;
    text-align: center;
  }
  .goods-price{
    margin-top: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    .integral{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_blue_icon.svg') top center no-repeat;
      background-size:14px;
    }
   /* .integral2{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_icon.png') top center no-repeat;
      background-size:14px;
    }
    .integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../assets/images/integral_grey_icon.svg') top center no-repeat;
      background-size:14px;
    }*/
    .textStyle{
      width: 14px;
      height: 14px;
      line-height: 14px;
    }
  }

  .disable{
    color: #CDCED4 !important;
    border:1px solid rgba(240,241,242,1)!important;
    span.integral{
      background: url('../../assets/images/integral_grey_icon.svg') top center no-repeat!important;
      background-size: 14px!important;
    }
  }
  .enable{
    color: #696D7F !important;
    border:1px solid rgba(205,206,212,1)!important;
    span.integral{
      background: url('../../assets/images/integral_icon.png') top center no-repeat!important;
      background-size: 14px!important;
    }
  }
  .choosed{
    color: #00BEBE !important;
    border: 1px solid #00BEBE !important;
    span.integral{
      background: url('../../assets/images/integral_blue_icon.svg') top center no-repeat!important;
      background-size: 14px!important;
    }
  }


  /*******滑动效果********/
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s ease;
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(250px);
    opacity: 0;
  }
</style>



