<template>
  <div class="banner_box">
    <swiper :options="swiperOption()" class="banner">
      <swiper-slide v-for="(item, index) in getBannerArray" :key="index">
        <img class="banner_img" :src="item.enlargedURL" alt="" @click="preview(index)" />
      </swiper-slide>
      <div v-show="getBannerArray&&getBannerArray.length>1" class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <p v-show="!selectedSku.available" class="sold_out">已下架</p>
    <p v-show="selectedSku.isSoldOut&&selectedSku.available" class="sold_out">已售罄</p>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  // import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
 // import 'swiper/dist/css/swiper.css'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {
    name: "swipeBanner",
    data(){
      return{
        pictureList:[
          {
            thumbnailURL:"https://cdn-app.nio.com/MER/2018/8/29/37d36e15-93f6-4704-ad1a-d18c0ab47c34.jpg",
            enlargedURL:"https://cdn-app.nio.com/MER/2018/8/29/8937b00e-7fd6-4862-b56f-e20a4ca9a0ae.jpg"
          },
          {
            thumbnailURL:"https://cdn-app.nio.com/MER/2018/8/29/154d5c14-13cf-4ab1-a859-646bb4b423e0.jpg",
            enlargedURL:"https://cdn-app.nio.com/MER/2018/8/29/89f22a8b-8982-4789-b338-e5ea1c15c7c9.jpg"
          },
          {
            thumbnailURL:"https://cdn-app.nio.com/MER/2018/8/29/f4735ef0-43ea-4c34-9d66-48ad064e6a52.jpg",
            enlargedURL:"https://cdn-app.nio.com/MER/2018/8/29/d144c42a-0539-4a98-9ba2-16a56a2df612.jpg"
          },
          {
            thumbnailURL:"https://cdn-app.nio.com/MER/2018/8/29/af6c02fd-7765-48d4-aa6a-4622af3ba8d0.jpg",
            enlargedURL:"https://cdn-app.nio.com/MER/2018/8/29/141fc0f7-6c14-47b3-8345-199b67c7a1b2.jpg"
          },
          {
            thumbnailURL:"https://cdn-app.nio.com/MER/2018/8/29/a41a3b3a-64cb-4578-b201-ab5b289fb957.jpg",
            enlargedURL:"https://cdn-app.nio.com/MER/2018/8/29/484ed775-640d-4f04-b87c-5c95d0f08f69.jpg"
          }
          ],
        // swiperOption: {
        //   pagination: {
        //     el: '.swiper-pagination',
        //   },
        //   autoplay: {
        //     delay: 5000,//5秒切换一次
        //   },
        //   loop : true,
        // },
      }
    },
    computed:{
      // imagesArray(){
      //   var array=[];
      //   this.selectedSku.pictureList.forEach((pic,ind)=>{
      //     array.push(pic.enlargedURL);
      //   });
      //   return array;
      // }
      ...mapState({
        'goodsState': state => state.goodsState.data
      }),
      getBannerArray(){
        let array = []; 
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "carousel"){
            // console.log(item)
            array = item.data;
          }
        })
        return array;
      },
      selectedSku(){
        let obj = {};
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"){
            // console.log(item)
            obj = item.data.selectedSku;
          }
        })
        return obj;
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    mounted(){},
    methods:{
      swiperOption(){
        return{
          pagination: {
            el: '.swiper-pagination',
          },
          autoplay: {
            delay: 5000,//5秒切换一次
          },
          loop : this.getBannerArray && this.getBannerArray.length>1
        }
      },
      // preview(index){
      //   if(util.isInWebview()){
      //     // banner埋点
      //     hybrid.buriedPointClick({
      //       eventKey:'MallItemPage_Banner_Click',
      //       currentPage:'MallItemPage_',
      //       NextPage:'',
      //       params:{
      //         spu_code:supreme.getQueryString("spuCode")
      //       }
      //     })
      //     hybrid.showImagesPreview({
      //       "images":this.imagesArray,
      //       "index":index
      //     })
      //   }
      // }
    }
  }
</script>

<style lang="scss" scoped>
  .banner_box{
    position: relative;
    left: 0;
    top: 0;
  }
  .banner{
    width:100%;
    height: 3.75rem;
  }
  .banner_img{
    display: block;
    width: 100%;
    height: 100%;
  }
  .sold_out{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color:#FFFFFF;
    font-size: 18px;
    background:rgba(4,11,41,0.4);
    z-index: 2;
  }
</style>



