<template>
  <ul class="home-goods-ul">
    <li @click="gotoGoodsPages(item.spu_code)" class="home-goods-li" v-for="item in goodsData" :key="item.spu_code">
      <div class="goods-banner">
        <img :src="item.img" alt="">
      </div>
      <div class="goods-name"><span>{{item.title}}</span></div>
      <div class="goods-price">
        <span v-if="item.price" class="integral"></span>
        <span v-if="item.price">{{item.price.originPricePoint}}</span>
        <span v-else>{{item.subtitle}}</span>
      </div>
    </li>
  </ul>
</template>

<script>
  import {mapState, mapMutations} from 'vuex'
  import util from '@/util/util'
  import {supreme} from '@/supreme-core'
  import baseConfig from '@/config/baseConfig'
  var pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
  var envInfo=util.getEnvInfo("envConfig")
  export default {
    name: "goodsList",

    data(){
      return{

      }
    },
    props:['goodsData'],
    created:function(){
      //supreme.showToast("xxxxxxxxx")
    },
    computed:{

    },
    methods:{
      gotoGoodsPages(supcode){
        // var url = "nio://user/login"
        let goodsUrl = `${envInfo.openUrl}${pathName}/goods?pageCode=${supcode}&wv=do`;
        if(util.isInWebview()){
          supreme.showToast(goodsUrl);
          dsBridge.call("NIODoBridge.openURL", goodsUrl)
        }else{
          //this.$router.push({ path: 'goods', query: { pageCode: supcode }})
          this.goto(pathName+"/goods",{pageCode: supcode })

        }

      }
    }
  }
</script>

<style lang="scss" scoped>
  .home-goods-ul{
    margin: 0 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .home-goods-li{
    margin-bottom: 15px;
    height: 205px;
    width: calc(96%/2);
    background-color: #fff;
    box-shadow:0px 3px 15px 0px rgba(60,69,90,0.15);
    border-radius:2px;
    .goods-banner{
      width: 100%;
      height: 120px;
      background-color: #c1c1c1;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .goods-name{
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 12px 32px 0;
      height: 34px;
      text-align: center;
    }
    .goods-price{
      margin-top: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: #666666;
      .integral{
        margin-right: 5px;
        width: 14px;
        height: 14px;;
        background: url('../../assets/images/integral_icon.png') top center no-repeat;
        background-size:14px;
      }
    }
  }
</style>



