// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import {supreme, type, taskMgr} from '@/supreme-core'
import baseConfig from '@/config/baseConfig'
import FastClick from 'fastclick';
import App from './App'
import {router} from './router'
import store from './store/store'
import '@/util/rem.js'
var pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
supreme.init({$router: router,defaultJumpPath:pathName+"/home?tab=1"});
//去除ios点击延迟
if ('addEventListener' in document) {
  document.addEventListener('DOMContentLoaded', function() {
      FastClick.attach(document.body);
  }, false);
}

var main={
  // 初始化基类
  initBaseData(callback){
    if(baseConfig.enableInfiniteScroll){
      var InfiniteScroll =  require("mint-ui").InfiniteScroll;
      Vue.use(InfiniteScroll);
    }
    if(baseConfig.enableImageLazyLoad){
      var Lazyload =  require("mint-ui").Lazyload;
      Vue.use(Lazyload);
    }
    if(baseConfig.enableMock){
      require('@/mock/mock');
    }
    if(type.isFunction(callback)){
      callback();
    }
  },
  initVue(){
    this.initBaseData(()=>{
      new Vue({
        el: '#app',
        router,
        store,
        render: h => h(App)
        // template: '<App/>',
        // components: { App }
      })
    });

  },
  init(){
    supreme.showToast('xxxx');
    dsBridge.register('appear',function(info){
      taskMgr.triggerVueComAppear();
    });

    dsBridge.register('networkChange',function(info){
      taskMgr.triggerVueNetworkChange(info);
    });
    // token
    let AccountToken = dsBridge.call("NIODoBridge.getAccountToken", "");
    supreme.sessionCache.set("accessToken", AccountToken);
    // 设备信息
    dsBridge.call("NIODoBridge.getOSInfo", "", function(string) {
      console.log(JSON.parse(string))
      supreme.sessionCache.set("getOSInfo",JSON.parse(string));
    })
    this.initVue();







    // hybrid.controlBackPress({isControl:true},function(){
      //taskMgr.triggerVueComBack()
    // });
    /**
    util.isInWebview()&&hybrid.controlWebviewAppear(function(){
      //self.back();
      taskMgr.triggerVueComAppear.bind(taskMgr);
    })
     */
  }

}
/* eslint-disable no-new */

main.init();



