import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/view/homePage/Index'
import goodsPage from '@/view/goodsPage/Index'
import confirmOrderPage from '@/view/confirmOrderPage/Index'
import myInvoicePage from '@/view/myInvoicePage/Index'
import errorPage from '@/view/errorPage/Index'
import baseConfig from '@/config/baseConfig'
Vue.use(Router);
var pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
var routers = [
  {
    path: '/',
    redirect:pathName+'/home'
  },
  {
    path: pathName+'/home',
    name: 'homePage',
    component: homePage,
    meta:{keepAlive: true}
  },
  {
    path: pathName+'/goods',
    name: 'goodsPage',
    component: goodsPage,
    // meta:{keepAlive: true}
  },
  {
    path: pathName+'/myInvoice',
    name: 'myInvoicePage',
    component: myInvoicePage,
    meta:{keepAlive: true}
  },
  {
    path: pathName+'/confirmOrder',
    name: 'confirmOrderPage',
    component: confirmOrderPage,
    meta:{keepAlive: true}
  },
  {
    path: '*',
    name: 'error',
    component: errorPage,
  }
]
var router = new Router({
  mode: baseConfig.routerModel,
  routes: routers
});
export {routers,router}



