import envConfigMapping from '@/config/env'
import { supreme, _} from '@/supreme-core'
export default {
  getEnv(){
    // 0 qa 1 uat 2 stg 3 prod
    var url= location.href;
    var code;
    if(location.protocol=="file:"){
      var osInfo = dsBridge.call("NIODoBridge.getOSInfo");
      if(osInfo&&osInfo.nio_app_env){
        if(osInfo.nio_app_env=='qa'){
          code=0;
        }else if(osInfo.nio_app_env=='sit'){
          code=1;
        }else if(osInfo.nio_app_env=='stg'){
          code=2;
        }else{
          code=3;
        }
      }else{
        code=1;
      }
      alert('xxxxxx');
      supreme.showToast(osInfo);
      return code;
    }
    if(url.indexOf('-qa.')!=-1||url.indexOf('localhost')!=-1||url.indexOf('127.0.0.1')!=-1 || url.indexOf('10.125.232.4')!=-1){
      code=0;
    }else if(url.indexOf('-uat.')!=-1){
      code=1;
    }else if(url.indexOf('-stg.')!=-1){
      code=2;
    }else{
      code=3;
    }
    var code=1;
    return code;
  },

  /**
   * @params 对应的配置文件的名称
   * */
  getEnvInfo(name){
    var env;
    var length=0;
    if(!name){
      for(var i in envConfigMapping){
        env=envConfigMapping[i];
      }
    }else{
      for(var i in envConfigMapping){
        envConfigMapping.length=length++;
        if(i==name){
          env=envConfigMapping[i]
        }
      }
    }

    if(envConfigMapping.length==0&&env==undefined){
      for(var i in envConfigMapping){
        env = envConfigMapping[i];
      }
    }
    if(env==undefined){
      throw ("envConfig:"+name+" 没有 匹配的配置文件");
      return ;
    }
    switch (this.getEnv()){
      case 0:
        return env.qa;
      case 1:
        return env.uat;
      case 2:
        return env.stg;
      case 3:
        return env.prod;
      default:
        return env.qa;
    }
  },
   //
  isInWebview () {
    console.log(dsBridge.hasNativeMethod("NIODoBridge.getOSInfo"));
    return  dsBridge&&dsBridge.hasNativeMethod("NIODoBridge.getOSInfo");
    // return true;
  },
  // 兼容刘海屏手机
  isIphoneX(){
    // let appInfo = JSON.parse(getCookie('appInfo')),
    //     deviceIdArr = ['iPhone10,3','iPhone10,6','iPhone11,2','iPhone11,4','iPhone11,6','iPhone11,8'],
    //     state = false;
    // if(appInfo==null){
    //   return false;
    // }
    // for(let value of deviceIdArr){
    //   if(value==appInfo['mobileinfo']['deviceId']){
    //     state = true;
    //   }
    // }
    // if(state){
    //   return true;
    // }else{
    //   return false;
    // }
    return false
  },
}



