<template>
  <div class="home-page">
    <headerNav @back="backToLastPage" v-if="!isInWebview"/>

    <div class="home-page-box">
      <!-- <ul class="home-page-but">
        <li v-for="(item,index) in butItem" :key="index" @click="testDsBridge(item)">{{item}}</li>
      </ul> -->
      <h3 class="home-page-title">车商城</h3>
      <div class="home-tabs">
        <div v-for="(item, index) in tabList" @click="switchTab(index)" :key="item.id" class="home-tabs-li">
          <span :class="{cur:tabIndex==index}">{{item.name}}</span>
        </div>
        <div class="home-tabs-wire" :style="{left:tabIndex==0?0:'86px'}"></div>
      </div>
      <swiper :options="swiperOption()" ref="mySwiper">
        <swiper-slide>
          <ul class="home-goods-ul">
            <li class="home-goods-li" v-for="(item,index) in portfolioList" :key="index">
              <div class="goods-banner">
                <img :src="item.homePicture" alt="">
              </div>
              <div class="goods-name"><span>{{item.name}}</span></div>
              <div class="goods-price">
                <!-- <span class="integral"></span> -->
                <span>{{item.priceSale}}</span>
              </div>
            </li>
          </ul>
        </swiper-slide>
        <swiper-slide>
          <goodsList v-for="item in homeListData" :key="item.sortKey"
            :goods-data="item.type=='double_goods_n_sub'||item.type=='double_goods_n_price'?item.data:[]" />
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
  import {mapState, mapMutations} from 'vuex';
  import {baseMixin, hybridFn, supreme} from '@/supreme-core'
  import headerNav from '@/components/header-nav.vue';
  import goodsList from './goodsList.vue';
  import 'swiper/dist/css/swiper.css'
  import util from '@/util/util'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import {
  portfolioListModel,
  tokenModel,
  homeListModel} from '@/model'
  export default {
    name: "homePage",
    mixins:[baseMixin],
    data(){
      return{
        tabList:[
          {name:'服务套餐',id:'tab01'},
          {name:'车辆附件',id:'tab02'}
        ],
        tabIndex:0,
        portfolioList:[],
        butItem:['getAccountToken','getOSInfo','Back', 'Toast','showLoading','hideLoading','disableWebviewPull','enableWebviewPull','showImagesPreview','isInWebview']
      }
    },
    appear(data){
      supreme.showToast('this is vue appear...........');
      console.log(data);
    },

    networkChange(status){
        //supreme.showToast("networkChage:"+status);
      supreme.showToast("networkChage:"+status);
    },
    // appregistered(){},
    created:function(){
      // native 头部调用demo
      if(util.isInWebview()){
        dsBridge.register("titleClick",function () {
          supreme.showToast("titleClick")
        });
        dsBridge.register("hongbao",function(){
          supreme.showToast("hongbao")
        })
        dsBridge.register("share",function(){
          supreme.showToast("share")
        })
        dsBridge.call("common.headerSetOption",{
          title:{
            text:'myTitlexxxxx',
            textColor:'2bbd96',
            icon:{
              url:'https://source.niohome.com/UserPicture/donghai.yao.jpg?t=1545117068506',
              position:'left'
            },
            callback:'titleClick'
          },
          rightColumn:[
            {
              text:"分享",
              textColor:'2bbd96',
              icon:{
                position:"right", // left right
                url:"https://www.kavo.co.jp/wp-content/themes/twentyeleven3/image/icon_facebook.jpg"
              },
              callback:"share"
            },
            {
              text:"关注",
              textColor:'2bbd96',
              icon:{
                position:"right", // left right
                url:"https://www.kavo.co.jp/wp-content/themes/twentyeleven3/image/icon_facebook.jpg"
              },
              callback:"share"
            }

          ],
          leftColumn:[
            {
              text:"1",
              textColor:'2bbd96',
              icon:{
                position:"left", // left right
                url:"https://www.kavo.co.jp/wp-content/themes/twentyeleven3/image/icon_facebook.jpg"
              },
              callback:"hongbao"
            },
            {
              text:"2",
              textColor:'2bbd96',
              icon:{
                position:"left", // left right
                url:"https://www.kavo.co.jp/wp-content/themes/twentyeleven3/image/icon_facebook.jpg"
              },
              callback:"hongbao"
            }

          ]
        })
      }

      this.initData();
    },
    mounted(){
      this.switchTab(this.getTab);
    },
    components: {
      headerNav,
      goodsList,
      swiper,
      swiperSlide
    },
    computed:{
      ...mapState({
        'homeListData': state => state.homeState.data
      }),
      swiper() {
        return this.$refs.mySwiper.swiper
      },
      getTab(){
        return this.$route.query.tab;
      },
      isInWebview(){
        return util.isInWebview();
      }
    },
    methods:{
      ...mapMutations({
        setData: "homeState/setData"
      }),
      backToLastPage(){
        // util.isInWebview()&&hybrid.close();
        // window.dsBridge.call("common.closeSyn");
      },
      testDsBridge(item){
        let dict = {"content": "显示消息"}
        let dictImages = {
            images: ["https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544530799687&di=c84047a2d010acbed4f6042e133ef324&imgtype=0&src=http%3A%2F%2Fimg.mp.itc.cn%2Fupload%2F20160831%2F99c25d4e319f4634bc985340f7fee3dd_th.jpg",
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544530799687&di=dbe4ae81c260213a7b564ecd52f17470&imgtype=0&src=http%3A%2F%2Fimg.alicdn.com%2Fi1%2FTB2uy9RppXXXXX5XXXXXXXXXXXX_%2521%2521660595271.jpeg"],
            index: 2
        };
        switch(item){
          case 'getAccountToken':
            let AccountToken = dsBridge.call("NIODoBridge.getAccountToken", "");
            if(AccountToken){
              alert(AccountToken)
              return;
            }
            dsBridge.call("NIODoBridge.openLoginPage", "", function(data){
              alert(data);
            });
            break;
          case 'Back':
            dsBridge.call("common.closeSyn");
            break;
          case 'Toast':
            dsBridge.call("common.showToast", dict)
            break;
          case 'showLoading':
            dsBridge.call("NIODoBridge.showLoading", "")
            setTimeout(()=>{
              dsBridge.call("NIODoBridge.hideLoading", "")
            },1000);
            break;
          case 'hideLoading':
            dsBridge.call("NIODoBridge.hideLoading", "")
            break;
          case 'disableWebviewPull':
            dsBridge.call("common.disableWebviewPull", "")
            break;
          case 'enableWebviewPull':
            dsBridge.call("common.enableWebviewPull", "")
            break;
          case 'showImagesPreview':
            dsBridge.call("NIODoBridge.showImagesPreview", dictImages)
            break;
          case 'getOSInfo':
            dsBridge.call("NIODoBridge.getOSInfo", "", function(string) {
              console.log(JSON.parse(string))
              alert(string)
            })
          case 'isInWebview':
            supreme.showToast(util.isInWebView());
            break;
        }
      },
      swiperOption(){
        let self = this;
        return{
          initialSlide:self.getTab,
          on:{
            slideChange: function(){
              self.tabIndex = this.activeIndex;
            },
          }
        }
      },
      switchTab(index){
        this.swiper.slideTo(index);
        this.tabIndex = index;
      },
      initData(){
        if(this.portfolioList.length<=0){
          this.getServiceList();
        }
        if(this.homeListData.length<=0){
          this.getCartShopping()
        }
      },
      getCartShopping(){
        let params = {
          Data:{source: 'cartShopping'}
        }
        homeListModel.execute(params).then((data)=>{
          let { resultDesc, resultData } = data.data;
          if(resultDesc == "success"){
            this.setData(resultData);
          }
        })
      },
      getServiceList(){
        portfolioListModel.post({category1Code:'PE,SO', activeLifeCode:'M01,D01'}).then((data)=>{
          let { resultDesc, resultData } = data.data;
          if(resultDesc == 'Success'){
            this.portfolioList = resultData;
          }
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
  .home-page-but{
    width: 100%;
    // height: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li{
      width: 48%;
      height: 60px;
      background-color: #00BEBE;
      font-size: 20px;
      color: #fff;
      line-height: 60px;
      text-align: center;
    }
  }
  .home-page {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 64px;
    overflow: hidden;
    background-color: #fff;
  }
  .home-page-box{
    width: 100%;
    height: 100%;
    overflow-x: scroll;
  }
  .home-page-title{
    margin-left: 25px;
    font-size: 32px;
    margin-bottom: 20px;
  }
  .home-tabs{
    margin-left: 25px;
    margin-bottom: 30px;
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 32px;
    width: 142px;
    .home-tabs-li{
      height: 30px;
      line-height: 30px;
      span{
        &.cur{
          color:#333;
        }
        color: #c1c1c1;
        font-size: 14px;
      }
    }
    .home-tabs-wire{
      transition: all .3s;
      position: absolute;
      left: 10px;
      bottom: 0;
      width: 56px;
      height: 2px;
      background-color: #00BEBE;
    }
  }
  .home-goods-ul{
    margin:0 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .home-goods-li{
    margin-bottom: 15px;
    height: 205px;
    width: calc(96%/2);
    background-color: #fff;
    box-shadow:0px 3px 15px 0px rgba(60,69,90,0.15);
    border-radius:2px;
    .goods-banner{
      width: 100%;
      height: 120px;
      background-color: #c1c1c1;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .goods-name{
      display: flex;
      align-items: center;
      margin: 12px 32px 0;
      height: 34px;
      text-align: center;
    }
    .goods-price{
      margin-top: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: #666666;
      .integral{
        margin-right: 5px;
        width: 14px;
        height: 14px;;
        background: url('../../assets/images/integral_icon.png') top center no-repeat;
        background-size:14px;
      }
    }
  }

  .home-goods-ul{
    margin: 30px 25px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .home-goods-li{
    margin-bottom: 15px;
    height: 205px;
    width: calc(96%/2);
    background-color: #fff;
    box-shadow:0px 3px 15px 0px rgba(60,69,90,0.15);
    border-radius:2px;
    .goods-banner{
      width: 100%;
      height: 120px;
      background-color: #c1c1c1;
    }
    .goods-name{
      display: flex;
      align-items: center;
      margin: 12px 32px 0;
      height: 34px;
      text-align: center;
    }
    .goods-price{
      margin-top: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: #666666;
      .integral{
        margin-right: 5px;
        width: 14px;
        height: 14px;;
        background: url('../../assets/images/integral_icon.png') top center no-repeat;
        background-size:14px;
      }
    }
  }
</style>



