<template>
  <div class="logisInfo" @click="selectAddress">
    <div v-show="addressShow">
      <span>{{ shipping_address.name }}</span>
      <span>{{ shipping_address.phone }}</span>
    </div>
    <div>
      <span :style="colorStyle1" v-if="addressShow">{{ shipping_address.province }}{{ shipping_address.city }}{{ shipping_address.county }}{{ shipping_address.detail }}</span>
      <span :style="colorStyle3" v-if="!addressShow">请点击前往地址管理添加地址</span>
      <span><img :src="img"></span>
    </div>
  </div>
</template>

<script>
  import Img from '@/assets/images/right_arrow_icon.png'
  import { getDetailAddressModel } from '@/model';
  export default {
    data(){
      return {
        img: Img,
        //字体颜色
        colorStyle1: {
          color: "#9B9DA9"
        },
        colorStyle2: {
          color: "#00BCBC"
        },
        colorStyle3: {
          color: "#363C54",
        },
        addressShow: false,
        addressId: '',
        shipping_address: {}
      }
    },
    props: {
      componentData: {
        type: Object,
        default: function(){
          return {}
        }
      }
    },
    appear(data){
      alert('1appear:'+JSON.stringify(info))
      console.log('this is vue appear...........');
      console.log(JSON.stringify(data));
    },
    mounted(){
      if(this.componentData){
        this.addressShow = true;
        this.shipping_address = this.componentData
      }else {
        this.addressShow = false
      }
      //this.getDetailAddress(this.addressId)
    },
    methods: {
      //获取默认地址
      getDetailAddress(id){
        getDetailAddressModel.post({
          Data: {
            address_id: id
          }
        }).then(data => {
          let info = data.data.resultData;
          this.shipping_address = info
          console.log(info)
        }).catch(err => {

        })
      },
      //跳转地址选择页面
      selectAddress(){
        // dsBridge.call("NIODoBridge.openAddressPage",'nio://mall/address"', (val) => {
        //   alert(val);
        //   this.addressId = val;
        //   this.getDetailAddress();
        // })
        console.log(this.openUrlData)
        dsBridge.call("NIODoBridge.openURL",this.openUrlData,(id) => {
          this.getDetailAddress(id)
        });
      },
    }
  }
</script>

<style scoped lang="scss">
  .logisInfo{
    width: 100%;
    padding: 0 25px;
    padding-bottom: 20px;
    border-bottom: 5px solid #F7F7F8;
    >div:nth-child(1){
      font-size: 16px;
      margin-bottom: 5px;
      span:nth-child(2){
        display: inline-block;
        margin-left: 15px;
      }
    }
    >div:nth-child(2){
      font-size: 14px;
      >span:nth-child(1){
        display: inline-block;
        width: 93%;
        /*display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;*/
      }
      >span:nth-child(2){
        display: inline-block;
        float: right;
        width: 7%;
        text-align: right;
        /*line-height: 20px;*/
        img{
          height: 10px;
        }
      }
    }
  }
</style>



