<template>
  <div class="wire_frame_page">
    <template v-for="(item,index) in componentsList">
      <component
       :is="item"
       :componentData="dataList[index]"
      >
      </component>
    </template>
  </div>
</template>

<script>
  import CarOrderInfo from '@/view/confirmorderPage/confirmOrderComponents/CarOrderInfo'
  import LogisInfo from '@/view/confirmorderPage/confirmOrderComponents/LogisInfo'
  import MyInvoice from '@/view/confirmorderPage/confirmOrderComponents/MyInvoice'
  import PayMode from '@/view/confirmorderPage/confirmOrderComponents/PayMode'
  import ProductsInfo from '@/view/confirmorderPage/confirmOrderComponents/ProductsInfo'
  export default {
    name: "WireframePage",
    components: {
      "v-logis-info": LogisInfo,
      "v-pay-mode": PayMode,
      "v-products-info": ProductsInfo,
      "v-my-invoice": MyInvoice,
      "v-car-order-info": CarOrderInfo,
    },
    data(){
      return {

      }
    },
    mounted(){
   //   console.log(this.componentsList)
   //   console.log(this.dataList)
    },
    props: {
      componentsList: {
        type: Array,
        default: function(){
          return []
        }
      },
      dataList: {
        type: Array,
        default: function(){
          return []
        }
      }
    },
    methods: {
      /*handlerClick(val){
        if(val === 'v-pay-mode'){
          this.$emit('change-pay-mode')
        }else if(val === 'v-my-invoice'){

        }
      }*/
    }
  }
</script>

<style scoped lang="scss">
  .wire_frame_page{

  }
</style>



