import { BaseModel } from '@/supreme-core'
import util from '@/util/util';
var envInfo = util.getEnvInfo("nrsEnvConfig")
var generateSign = require('@/util/generateSign')
class TokenModel extends BaseModel{
  constructor(config={}){
    config.baseURL =envInfo.tokenUrl+ envInfo.application;
    super(config);
  }
  post(param){
    if(!param){
      param={};
    }
    var signObj = generateSign.default()
    //param["token"]=supreme.sessionCache.get("token").token;
    param["timestamp"]=signObj.timestamp;
    param["sign"]=signObj.getwaySign;
    //param["_sid"]=supreme.sessionCache.get("user")._sid;
    return super.post(param,this.config);
  }
  get(param){
    return super.get(param);
  }
}
export default TokenModel;



