<template>
  <div class="invoice" @click="selectInvoice">
    <div>
      <div>我的发票：</div>
      <div :style="invoiceStyle.style">{{ invoiceStyle.text }}</div>
    </div>
    <div><img :src="img"></div>
  </div>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import baseConfig from '@/config/baseConfig'
  import Img from '@/assets/images/right_arrow_icon.png'
  import {baseMixin, hybridFn, supreme} from '@/supreme-core'
  export default {
    data(){
      return {
        img: Img,
        //发票信息
        invoiceStyle: {
          style: {
            color: '#9B9DA9',
          },
          text: '请填写发票信息'
        },
        invoice: {

        },
      }
    },
    computed: {
      ...mapState({
        //发票信息
        'invoiceData': state => state.myInvoiceState.invoiceData,
      })
    },
    activated(){
      let invoiceData = this.invoiceData;
      if(invoiceData.type){
        this.invoice.type = invoiceData.type;
        this.invoiceStyle.style.color = '#363C54';
        if(this.invoice.type === 1){
          this.invoice.email = invoiceData.data.mail;
          this.invoiceStyle.text = '个人发票'
        }else if (invoiceData.type === 2){
          this.invoice.companyName = invoiceData.data.companyName;
          this.invoice.orgCode = invoiceData.data.companyCode;
          this.invoice.email = invoiceData.data.companyMail;
          this.invoiceStyle.text = '企业发票'
        }
       // console.log(this.invoice)
      }
    },
    methods: {
      //跳转发票页面
      selectInvoice(){
        let pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
        supreme.goto(pathName + '/myInvoice',{})
      },
    }
  }
</script>

<style scoped lang="scss">
  .invoice{
    width: 100%;
    height: 55px;
    padding: 0 25px;
    border-bottom: 5px solid #F7F7F8;
    >div:nth-child(1){
      float: left;
      width: 93%;
      height: 100%;
      >div:nth-child(1){
        float: left;
        height: 100%;
        line-height: 50px;
        font-size: 14px;
      }
      >div:nth-child(2){
        float: right;
        height: 100%;
        line-height: 50px;
        font-size: 14px;
        color: #CDCED4;
      }
    }
    >div:nth-child(2){
      float: left;
      width: 7%;
      height: 50px;
      text-align: right;
      line-height: 50px;
      img{
        height: 10px;
      }
    }
  }
</style>



