<template>
  <div class="carOrderInfo">
    <div>
      <div>关联车辆：</div>
      <div>{{ relatedCarInfo.carName }}</div>
    </div>
    <div>
      <div>购车订单号：</div>
      <div>{{ relatedCarInfo.carOrderNo }}</div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import {baseMixin, hybridFn, supreme} from '@/supreme-core'
  export default {
    data(){
      return {
        relatedCarInfo: {}
      }
    },
    /*computed: {
      ...mapState({
        //关联车辆信息
        'relatedCarInfo': state => state.confirmOrderState.goodsPageData.relatedCarInfo,
      })
    },*/
    created(){
      let data = supreme.sessionCache.get("orderData");
      this.relatedCarInfo = data.relatedCarInfo;
    },
  }
</script>

<style scoped lang="scss">
  .carOrderInfo{
    width: 100%;
    height: 85px;
    padding: 15px 25px;
    border-bottom: 5px solid #F7F7F8;
    color: #363C54;
    font-size: 14px;
    >div{
      width: 100%;
      height: 25px;
      >div{
        height: 100%;
        line-height: 25px;
      }
      >div:nth-child(1){
        float: left;
      }
      >div:nth-child(2){
        float: right;
      }
    }
  }
</style>



