<template>
  <div class="payMode" @click="handlerPayModeChange">
    <div>
      <div>
        <div>付款方式：</div>
        <div style="margin-top: 2px;">
          <span style="float: left" :style="colorStyle1">我的积分：</span>
          <div class="goods-price" style="float: left">
            <span class="integral2"></span>
            <span :style="colorStyle1">{{ parseInt(payData.userIntegration) }}</span>
          </div>
        </div>
      </div>
      <div>
        <div class="goods-price" style="float: left">
        <span class="integral3"></span>
        <span :style="colorStyle3">6,980</span>
      </div>
        <div class="plus" :style="colorStyle3">+</div>
        <div class="goods-price" style="float: left" :style="colorStyle3">
          <span class="textStyle">￥</span>
          <span>{{ parseInt(payData.totalPoint)/parseInt(payData.exchangeRate) }}</span>
        </div>
      </div>
    </div>
    <div><img :src="img"></div>
  </div>
</template>

<script>
  import { supreme } from '@/supreme-core'
  import Img from '@/assets/images/right_arrow_icon.png'
  export default {
    data(){
      return {
        img: Img,
        //字体颜色
        colorStyle1: {
          color: "#9B9DA9"
        },
        colorStyle2: {
          color: "#00BCBC"
        },
        colorStyle3: {
          color: "#363C54"
        },
        //支付金额
        payData: {
          exchangeRate: 1,
          minMoney: 0,
          minPoint: 0,
          payPoint: 0,
          totalPoint: 0,
          userIntegration: 0,
        },
      }
    },
    props: {
      componentData: {
        type: Object,
        default: function(){
          return {}
        }
      }
    },
    mounted(){
    //  console.log(this.componentData)
      this.payData = this.componentData
    },
    methods: {
      handlerPayModeChange(){
        supreme.pubHubus.$emit('change-pay-mode')
      }
    }
  }
</script>

<style scoped lang="scss">
  .payMode{
    touch-action: none;
    width: 100%;
    padding: 0 25px;
    border-bottom: 5px solid #F7F7F8;
    height: 75px;
    >div:nth-child(1){
      float: left;
      width: 93%;
      height: 100%;
      >div:nth-child(1){
        float: left;
        height: 100%;
        padding: 15px 0;
        >div:nth-child(1){
          font-size: 14px;
        }
        >div:nth-child(2){
          font-size: 14px;
        }
      }
      >div:nth-child(2){
        float: right;
        height: 100%;
        padding: 25px 0;

      }
    }
    >div:nth-child(2){
      float: left;
      width: 7%;
      height: 75px;
      text-align: right;
      line-height: 75px;
      img{
        height: 10px;
      }
    }
  }
  .plus{
    float: left;
    width: 8px;
    height: 17px;
    font-size: 14px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 5px;
  }
  .goods-name{
    display: flex;
    align-items: center;
    margin: 12px 32px 0;
    height: 34px;
    text-align: center;
  }
  .goods-price{
    margin-top: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: #666666;
    .integral,.integral2,.integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_blue_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .integral2{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_icon.png') top center no-repeat;
      background-size:14px;
    }
    .integral3{
      margin-right: 5px;
      width: 14px;
      height: 14px;;
      background: url('../../../assets/images/integral_grey_icon.svg') top center no-repeat;
      background-size:14px;
    }
    .textStyle{
      width: 14px;
      height: 14px;
      line-height: 14px;
    }
  }
</style>



