import { BaseStore } from '@/supreme-core';
import { productDetailModel } from '@/model'

let state = {
  pageState:{
    selectCarShow: false,
    amount:1
  },
  data:{
    
  }
}
let getters = {
  // 选中车辆
  currentSelectCar(state) {
    let current = {};
    state.data.goodsComponent&&state.data.goodsComponent.forEach((item)=>{
      if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList){
        current = item.data.vehicleRelationInfoList.filter(todo => todo.selected)[0];
      }
    })
    return current?current:null;
  },
  // 已选规格属性
  selectedAttr(state){
    let text = "";
    state.data.goodsComponent&&state.data.goodsComponent.forEach((item)=>{
      if(item.type === "goods_specification"&&item.data.serialAttributes!=null){
         // 行
        item.data.serialAttributes.forEach((attrItem)=>{
          // 列
          attrItem.spuAttributeValueList.forEach((selectList)=>{
            if(selectList.state){
              text += selectList.attributeValue + " ";
            }
          })
        })
      }
    })
    return text + `${text!=""?"x"+state.pageState.amount:""}`;
  }
}
let mutations = {
  setGoodsData(state, data){
    state.data = data;
  },
  toggleCarLayer(state, data){
    state.pageState.selectCarShow = data;
  },
  selectCar(state, data){
    state.data.goodsComponent.forEach((item)=>{
      if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList){
        item.data.vehicleRelationInfoList.forEach(carList => {
          if(carList.carOrderNo === data){
            carList.selected = true;
          }else{
            carList.selected = false;
          }
        });
      }
    })
  }
}
let actions = {
  getGoodsData({commit, state}, params){
    productDetailModel.execute(params).then((data)=>{
      let { resultDesc, resultData } = data.data;
      if(resultDesc=="success"){
        resultData.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList){
            // 添加sku点击选中属性
            item.data.serialAttributes&&item.data.serialAttributes.forEach((attrItem)=>{
              attrItem.spuAttributeValueList.forEach((selectList)=>{
                selectList.status = "";
              })
            })
            // 添加默认选中车辆
            item.data.vehicleRelationInfoList&&item.data.vehicleRelationInfoList.forEach((carItem, carIndex)=>{
              if(carIndex==0){
                carItem.selected = true;
              }else{
                carItem.selected = false;
              }
            })
          }
        })
        // 同步详情数据
        commit('setGoodsData', resultData);
      }else{
        commit('setGoodsData', {});
      }
    })
  }
}
var store= BaseStore.getInstance({
  state: state,
  mutations:mutations
});
export default {
  namespaced: true,
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
}



