<template>
  <transition name="slide-fade">
    <div v-show="pageState.selectCarShow" class="select-car-layar">
      <headerNav @back="backToLastPage" :shareShow="false" />
      <div class="select-car-content">
        <p class="title">选择关联车辆</p>
        <ul class="car-list">
          <li v-for="item in carArrayData" @click="changCar(item.carOrderNo)" :key="item.carOrderNo">
            <p>{{item.vehicleInfo.carName}} · {{item.vehicleInfo.carColor}} </p>
            <p>{{item.userVinCode?"车辆车架号：":"购车订单号："}}{{item.userVinCode?item.vinCode:item.carOrderNo}}</p>
            <!-- <p v-else>{{item.carOrderNo}}</p> -->
            <!-- <p v-else>车辆车架号：{{item.vinCode}}</p> -->
            <span v-show="item.selected" class="select_icon"></span>
          </li>
        </ul>
      </div>
      <div class="select-car-confirm" @click="toggleCarLayer(false)"> 
        <div class="confirm-but"><span>确定</span></div>
      </div>
    </div>
  </transition>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  import util from '@/util/util'
  import headerNav from '@/components/header-nav.vue';
  export default {
    name: "selectCarLayar",
    data(){
      return{
      }
    },
    computed:{
      ...mapState({
        'pageState': state => state.goodsState.pageState,
        'goodsState': state => state.goodsState.data
      }),
      carArrayData(){
        let array = [];
         this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList){
            array = item.data.vehicleRelationInfoList;
          }
        })
        return array;
      }
    },
    components: {
      headerNav
    },
    mounted(){
      
    },
    methods:{
      ...mapMutations({
        toggleCarLayer:"goodsState/toggleCarLayer",
        selectCar:"goodsState/selectCar",
      }),
      backToLastPage(){
        this.toggleCarLayer(false);
      },
      closeLaye(){
        this.toggleCarLayer(false);
      },
      changCar(id){
        this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"&&item.data.vehicleRelationInfoList){
            item.data.vehicleRelationInfoList.forEach(carList => {
              if(carList.carOrderNo === id){
                carList.selected = true;
              }else{
                carList.selected = false;
              }
            });
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
      transition: all .2s ease;
  }
  .slide-fade-leave-active {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter,.slide-fade-leave-to {
      transform: translateX(600px);
      opacity: 0;
  }
  .select-car-layar{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding-top: 64px;
    z-index: 200;
    .title{
      padding-left: 25px;
      color: #363C54;
      font-size: 30px;
    }
  }
  .select-car-content{
    width: 100%;
    height: 100%;
    overflow-x: scroll;
  }
  .car-list{
    margin: 23px 25px 0;
    border-top: 1px solid #F0F1F2;
    li{
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      height: 90px;
      border-bottom: 1px solid #F0F1F2;
      p{
        font-size: 14px;
        color: #363C54;
      }
      .select_icon{
        position: absolute;
        right: 0;
        top: 33px;
        width: 16px;
        height: 16px;
        background: url("../../assets/images/select_car_icon.png") center no-repeat;
        background-size: 16px;
      }
    }
  }
  .select-car-confirm{
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 70px;
    padding: 0 25px;
    background-color: #fff;
    border-top: 1px solid #F0F1F2;
    .confirm-but{
      width: 100%;
      height: 45px;
      line-height: 45px;      
      text-align: center;
      background-color: #00BEBE;
      border-radius: 23px;
      font-size: 16px;
      color: #FFF;
    }
  }
</style>



