<template>
  <transition name="slide-fade">
    <div v-show="specLayerShow" class="spec-layer">
      <div class="spec-layer-box" @click.self="closeLaye">
        <!-- 商品信息 -->
        <div class="spec-info-box">
          <div class="spec-info">
            <span @click.stop="closeLaye" class="close"></span>
            <div class="spec-info-image">
              <img :src="getBannerUrl" alt="">
            </div>
            <div class="spec-info-text">
              <div class="price">
                <span class="integral">{{selectedSku.price&&selectedSku.price.currentUserPricePoint}}</span>
                <span>¥{{selectedSku.price&&selectedSku.price.currentUserPriceCash}} </span>
              </div>
              <div class="select-sku-text">
                <p>年套餐 x{{pageState.amount}}</p>
                <span>库存：{{selectedSku.remain}}</span>
              </div>
            </div>
          </div>
        </div>
        <!-- 可滚动区域 -->
        <div class="spec-scroll">
          <!-- 车辆选择 -->
          <div v-if="currentSelectCar&& currentSelectCar.vehicleInfo" class="spec-scroll-relevance" @click="toggleCarLayer(true)">
            <span>关联车辆：</span>
            <p>{{currentSelectCar.vehicleInfo.carName}}·{{currentSelectCar.vehicleInfo.carColor}}</p>
            <p>购车订单号：{{currentSelectCar.carOrderNo}}</p>
            <span class="arrow"></span>
          </div>
          <!-- 套餐选择 -->
          <div class="spec-scroll-select" v-for="AttrItem in serialAttributes" :key="AttrItem.attributeCode">
            <span class="title">{{AttrItem.attributeName}}：</span>
            <ul class="select-ul">
              <li v-for="Value in AttrItem.spuAttributeValueList" @click="selectSkuList(AttrItem,Value)" :class="{selected:Value.state}" :key="Value.valueCode">{{Value.attributeValue}}</li>
              <!-- <li :class="{selected:true}">年套餐</li> -->
            </ul>
          </div>
          <!-- 数量 -->
          <div class="spec-scroll-select">
            <span class="title">数量选择 (库存 {{selectedSku.remain}})：</span>
            <div class="number">
              <span class="reduce" @click="amountControl('-')"></span>
              <span>{{pageState.amount}}</span>
              <span class="add" @click="amountControl('+')"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import { mapState, mapGetters, mapMutations } from 'vuex'
  // import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  export default {
    name: "SpecLayer",
    data(){
      return{
      }
    },
    props:['specLayerShow'],
    computed:{
      ...mapState({
        'goodsState': state => state.goodsState.data,
        'pageState': state => state.goodsState.pageState
      }),
      ...mapGetters({
        currentSelectCar: 'goodsState/currentSelectCar'
      }),
      getBannerUrl(){
        let url = ""; 
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "carousel"){
            url = item.data[0].thumbnailURL;
          }
        })
        return url;
      },
      selectedSku(){
        let obj = {}; 
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"){
            obj = item.data.selectedSku
          }
        })
        return obj;
      },
      // 可选属性
      serialAttributes(){
        let array = [];
        this.goodsState.goodsComponent&&this.goodsState.goodsComponent.forEach((item)=>{
          if(item.type === "goods_specification"){
            array = item.data.serialAttributes
          }
        })
        return array;
      }
    },
    components: {
     
    },
    mounted(){
     
    },
    methods:{
      ...mapMutations({
        toggleCarLayer:"goodsState/toggleCarLayer",
      }),
      closeLaye(){
        this.$emit('close');
      },
      selectSkuList(lineItem, columnAttr){
        // 行
        this.serialAttributes.forEach((attrItem)=>{
          if(attrItem.attributeCode == lineItem.attributeCode){
            // 列
            attrItem.spuAttributeValueList.forEach((selectList)=>{
              if(selectList.valueCode == columnAttr.valueCode){
                selectList.state = true;
              }else{
                selectList.state = false;
              }
            })
          }
        })
      },
      amountControl(data){
        if(this.pageState.amount<2&&data=="-"){
          return;
        }
        switch(data){
          case "-":
            this.pageState.amount--;
            break;
          case "+":
            this.pageState.amount++;
            break;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
      transition: all .2s ease;
  }
  .slide-fade-leave-active {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter,.slide-fade-leave-to {
      transform: translateY(600px);
      opacity: 0;
  }
  .spec-layer{
    position: absolute;
    left: 0;
    bottom: 0;
    height:100%;
    width: 100%;
    z-index: 100;
  }
  .spec-layer-box{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    padding-bottom: 70px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
  }
  .spec-info-box{
    position: relative;
    padding: 0 25px;
    background-color: #fff;
    .close{
      position: absolute;
      right: 22px;
      top: 23px;
      width: 16px;
      height: 16px;
      background: url("../../assets/images/close_icon.png") center no-repeat;
      background-size: 16px;
    }
  }
  
  .spec-info{
    display: flex;
    padding: 20px 0;
    border-bottom: 1px solid #F0F1F2;
    .spec-info-image{
      width: 80px;
      height: 80px;
      background-color: #CDCED4;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .spec-info-text{
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    flex: 1;
    .price{
      font-size: 14px;
      color: #363C54;
      span{
        margin-right: 10px;
      }
      .integral{
        padding-left: 16px;
        width: 14px;
        height: 14px;
        background: url("../../assets/images/integral2_icon.png") left 3px no-repeat;
        background-size: 14px 12px;
      }
    }
    .select-sku-text{
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      font-size: 12px;
      color: #363C54;
      span{
        color: #9B9DA9;
      }
    }
  }

  .spec-scroll{
    max-height: 60%;
    overflow-x: scroll;
    padding: 0 25px 30px;
    background-color: #fff;
  }
  .spec-scroll-relevance{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 14px 0;
    border-bottom: 1px solid #F0F1F2;
    .arrow{
      position: absolute;
      right: 5px;
      bottom: 40px;
      width: 6px;
      height: 10px;
      background: url("../../assets/images/right_arrow_icon.png") center no-repeat;
      background-size: 6px 10px;
    }
    span{
      color: #9B9DA9;
      font-size: 14px;
      margin-bottom: 5px;
    }
    p{
      font-size: 14px;
      color: #363C54;
    }
  }
  .spec-scroll-select{
    margin-top: 20px;
    .title{
      color: #9B9DA9;
      font-size: 14px;
    }
    .select-ul{
      display: flex;
      flex-wrap: wrap;
      li{
        margin-top: 15px;
        margin-right: 20px;
        height: 30px;
        line-height: 30px;
        padding: 0 19px;
        border-radius: 15px;
        border: 1px solid #CDCED4;
        color: #696D7F;
        &.selected{
          border: 1px solid #00BCBC;
          color: #00BCBC;
        }
      }
    }
    .number{
      margin-top: 15px;
      width: 110px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border: 1px solid #CDCED4;
      border-radius: 15px;
      span{
        flex: 1;
        font-size: 14px;
        color: #363C54;
        text-align: center;
      }
      .reduce{
        height: 30px;
        background: url("../../assets/images/reduce_icon.png") center no-repeat;
        background-size: 10px;
        &.disabled{
          background: url("../../assets/images/reduce_disabled_icon.png") center no-repeat;
          background-size: 10px;
        }
      }
      .add{
        height: 30px;
        background: url("../../assets/images/add_icon.png") center no-repeat;
        background-size: 10px;
        &.disabled{
          background: url("../../assets/images/add_disabled_icon.png") center no-repeat;
          background-size: 10px;
        }
      }
    }
  }
</style>



