import Vue from "vue"
import Vuex from 'vuex'

import homeState from './homePage/state'
import goodsState from './goodsPage/state'
import confirmOrderState from './confirmOrderPage/state'
import myInvoiceState from './myInvoicePage/state'
Vue.use(Vuex);
export default new Vuex.Store({
   modules:{
    'homeState': homeState,
    'goodsState': goodsState,
    'confirmOrderState': confirmOrderState,
    'myInvoiceState': myInvoiceState
  }
});



